import { showAlert } from "./alert";
import axios from "axios";

export const createMail = async (data) => {
    try {
      const res = await axios({
        method: 'POST',
        url: `http://localhost:3000/api/v1/mail`,
        data
      });
        const mail = (res.data.data.mail._id);
        if (res.data.status === 'success') {
            const myLink = document.getElementById("id");
            myLink.setAttribute("value", mail);
        } 
    }catch (err) {
      showAlert('error', err.response.data.message);
    }
}


export const deleteMail = async (data) => {
    try{
        const res = await axios({
            method: 'DELETE',
            url: `http://localhost:3000/api/v1/mail/${data}`,
        });
       if(res)
         {      
            showAlert('success', 'Mail Deleted Successfully');
             location.reload(true);
         }
    }
    catch (err) {
        showAlert('error', err.response.data.message);
    }   
}

